.body{

    position: relative;
    background: #00FFCC;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header{
    top:0px;
    height: 150px;
    background-color: #ABABAB;
    width: 100%;
    display: flex;
    align-items: center;
    position: sticky;
}

.stateMic{
    display: flex;
    align-items: center;
}

.rotate{
    transform: rotate(180deg);
}

.micIgnore{
    color:red;
    font-size: 50px;
    margin-left: 25px;
}

.micActive{ 
    color:#00FF0A;
    font-size: 50px;
    margin-left: 25px;
}

.state{
    width: 300px;
    margin-left: 110px;
    font-size: 50px;
    color: white;
}

.section{
    width: 95%;
    border: 2px solid black;
    border-radius: 29px;
    background: white;
    margin-top: 10px;
    padding-bottom: 30px;
    margin-bottom: 30px;
}

.section_item{
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr 7fr;
}

.section_item_field{
    text-align: center;
    width: 435px;
    font-weight: 700;
    margin-left: 50px;
    font-size: 40px;
    margin-top: 0px;
}

.section_item_field_active{
    border-radius: 31px;
    background-color: #00FFCC;
    text-align: center;
    width: 435px;
    font-weight: 700;
    margin-left: 50px;
    font-size: 40px;
}

.section_item_textarea{ 
    color: black;
    padding-top: 0px;
    padding-bottom: 0px;
    padding-left: 30px;   
    border: 2px solid black;
    border-radius: 31px;
    margin-left: 100px;
    margin-right: 50px;
    font-weight: 700;
    font-size: 40px;
    align-self: flex-start;
}

.section_item_textarea::placeholder{
    font-family: "Roboto", sans-serif;
}

.advice{
    right: 100px;
    position: absolute;
    padding-left: 30px;
    padding-right: 30px;
    height: 100px;
    color: white;
    background-color: #6200EE;
    border-radius: 41px;
    display: flex;
    align-items: center;
    font-size: 30px;
}

.imgDiv{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 50px;
    border: 5px solid white;
    border-radius: 7px;
    font-weight: bold;
}

.modal{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 30px;
}

.advice_item{
    margin-top: 30px;
    display: flex;
    align-items: center;
    font-size: 30px;
}

.advice_span{
    font-weight: bold;
}

.imgDivArrow{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 50px;
    border: 5px solid white;
    border-radius: 7px;
}

.enterImgDiv{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 60px;
    border: 5px solid white;
    border-radius: 7px;
    font-weight: bold;
}

.imgDivP{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border: 5px solid white;
    border-radius: 7px;
    font-weight: bold;
}

.shiftImgDiv{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 60px;
    border: 5px solid white;
    border-radius: 7px;
    font-weight: bold;
}