.modal{
    z-index: 100;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: none;
    opacity: 0;
    transition: 0.5s;
}

.modal_active{
    z-index: 100;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 1;
    transition: 0.5s;
    pointer-events: all;
}

.modal_content{
    color: white;
    width: 60%;
    background-color: #6200EE;
    border-radius: 41px;
    padding: 20px;
    border-radius: 12px;
    transform: scale(0.5);
    transition: 0.4s all;
}

.modal_contentActive{
    color: white;
    padding: 20px;
    width: 60%;
    background-color: #6200EE;
    border-radius: 41px;
    transform: scale(1);
    transition: 0.4s all;
}

@media (max-width: 991px) {

    .modal_contentActive{
        width: 70vw;
    }

    .modal_content{
        width: 70vw;
    }

}

